export default "body {\n  background: #C5E1A5;\n}\n\nform {\n  width: 60%;\n  margin: 60px auto;\n  background: #efefef;\n  padding: 60px 120px 80px 120px;\n  text-align: center;\n  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);\n}\n\nlabel {\n  display: block;\n  position: relative;\n  margin: 40px 0px;\n}\n\n.label-txt {\n  position: absolute;\n  top: -1.6em;\n  padding: 10px;\n  font-family: sans-serif;\n  font-size: 0.8em;\n  letter-spacing: 1px;\n  color: #787878;\n  transition: ease 0.3s;\n}\n\n.input {\n  width: 100%;\n  padding: 10px;\n  background: transparent;\n  border: none;\n  outline: none;\n}\n\n.line-box {\n  position: relative;\n  width: 100%;\n  height: 2px;\n  background: #BCBCBC;\n}\n\n.line {\n  position: absolute;\n  width: 0%;\n  height: 2px;\n  top: 0px;\n  left: 50%;\n  transform: translateX(-50%);\n  background: #0B1B3B;\n  transition: ease 0.6s;\n}\n\n.input:focus + .line-box .line {\n  width: 100%;\n}\n\n.label-active {\n  top: -3em;\n}\n\nbutton {\n  display: inline-block;\n  padding: 12px 24px;\n  background: gainsboro;\n  font-weight: bold;\n  color: #787878;\n  border: none;\n  outline: none;\n  border-radius: 3px;\n  cursor: pointer;\n  transition: ease 0.3s;\n}\n\nbutton:hover {\n  background: #0B1B3B;\n  color: #ffffff;\n}\n/*# sourceMappingURL=data:application/json;base64, */";